@import url("https://fonts.googleapis.com/css?family=Roboto:300,300i,400,400i,500");
@import url("https://fonts.googleapis.com/css?family=Yanone+Kaffeesatz:300,400,700");
@import url("https://fonts.googleapis.com/css?family=Slabo+27px");
.main-index .navbar {
  background-color: #fff;
  transition: all .25s;
  border: none;
  -webkit-box-shadow: 0px 10px 35px -10px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 0px 10px 35px -10px rgba(0, 0, 0, 0.75);
  box-shadow: 0px 10px 35px -20px rgba(0, 0, 0, 0.5);
}

.main-index .navbar .content-holder .nav-link {
  display: inline-block;
  line-height: 70px;
}

.main-index .navbar .content-holder .boards-menu-link {
  padding: 0px 25px;
  text-decoration: none;
  transition: all .25s;
}

.main-index .navbar .content-holder .boards-menu-link:hover {
  border-color: #b2b3b7;
  color: #b2b3b7;
}

.main-index .navbar .content-holder .boards-menu-link:hover .fa-th-large {
  color: #b2b3b7;
}

.main-index .navbar .left-container {
  padding-left: 0px;
}

.main-index .navbar .left-container .boards {
  color: #b2b3b7;
}

.main-index .navbar .left-container .boards .fa-th-large {
  transition: all .25s;
  color: #d6d6d6;
  margin-right: 8px;
  font-size: 22px;
  position: relative;
  top: 3px;
}

.main-index .navbar .center-container {
  text-align: center;
  margin: 0 auto;
}

.main-index .navbar .center-container img {
  margin: 0 auto;
}

.main-index .navbar .right-container {
  padding-right: 0px;
  text-align: right;
}

.main-index .navbar .right-container ul {
  margin: 0px;
  padding: 0px;
}

.main-index .navbar .right-container ul li {
  display: inline-block;
  list-style: none;
}

.main-index .navbar .right-container .hamburger-menu {
  color: #d6d6d6;
  border-left: 1px solid #d6d6d6;
  font-size: 20px;
  height: 100%;
  background-color: transparent;
}

.main-index .navbar .right-container .notifs-btn {
  color: #d6d6d6;
  font-size: 18px;
  background-color: transparent;
}

.main-index .navbar .right-container .profile {
  padding: 0px;
}

.main-index .navbar .right-container .profile .profile-img {
  max-width: 30px;
}

.main-index .navbar .right-container .profile .profile-welcoming {
  padding: 0px 5px;
  font-size: 16px;
}

.main-index .navbar .right-container .profile .profile-welcoming:hover {
  color: #4687ff;
}

.main-index .navbar .right-container .dropdown.open .dropdown-toggle {
  color: #b2b3b7;
  background-color: #fff;
  border-color: #b2b3b7;
}

@media (max-width: 767px) {
  .main-index .navbar .content {
    height: 70px;
    padding: 0px !important;
  }
  .main-index .navbar .content .logo-container .logo {
    font-size: 23px;
  }
  .main-index .navbar .content .logo-container .logo .logo-initials {
    font-size: 25px;
  }
  .main-index .navbar .content .contact-link-container .contact-link {
    font-size: 16px;
    padding: 5px 20px;
  }
  padding: 0px !important;
}
